import { Component } from '@angular/core';
import {
  App, IonicPage, NavController, NavParams, ViewController, AlertController, ActionSheetController,
  LoadingController
} from 'ionic-angular';
import { Api } from '../../providers/api/api';
import { ImagePicker } from '@ionic-native/image-picker';
import {Storage} from "@ionic/storage";
import {LoginPage} from "../login/login";
import { Camera, CameraOptions } from '@ionic-native/camera';
import { appSettings } from '../../app/app-settings';

@IonicPage()
@Component({
  selector: 'page-profile',
  templateUrl: 'profile.html',
})
export class ProfilePage {

  user: any = {};
  timerNextPopy: any;
  maxTime: number;
  remainingTime: string;
  hidevalue: boolean=true;

  constructor(public navCtrl: NavController,
    public navParams: NavParams,
    public viewCtrl: ViewController,
    public imagePicker: ImagePicker,
    public app: App, 
    public api: Api,
    public storage: Storage,
    public alertCtrl: AlertController,
    public camera: Camera,
    public actionSheetCtrl: ActionSheetController,
    public loadingCtrl: LoadingController) {

   this.storage.get('isConnected').then((val) => {
    if (!val) {
      this.navCtrl.setRoot(LoginPage);
    }
  });

   this.storage.get('user').then((userStorage) => {
    if (userStorage) {
      this.storage.get('fromLoading').then((fromLoading) => {
        if(fromLoading){
          this.user = userStorage;
          this.storage.remove('fromLoading');
        }else{
          this.loadUserInfo(userStorage.id);          
        }

        this.storage.get('configurations').then((configurations) => {
          this.maxTime = +configurations['BLOCK_ADD_POPY_DURATION'].value - (this.user.dateServer - this.user.lastPopup);
          this.startTimer();          
        });

      });
    }
  });   

 }

 loadUserInfo(idUser){
  let loading = this.loadingCtrl.create({
    spinner: 'crescent',
    content: 'Chargement de donnÃ©es ...',
    dismissOnPageChange: true
  });

  loading.present();

  let data: any;
  data = {};

  this.api.post('getUser/' + idUser, data)
  .subscribe(
    (data) => {
      let body: any;
      body = JSON.parse(data.text());

      this.user =body;
      this.storage.set('user', body);

      loading.dismiss();

    },
    (err) => {
      this.storage.remove('user');
      this.storage.remove('isConnected');
      this.navCtrl.setRoot(LoginPage);
    },
    () => {
                          //this.goToHome();
                        }
                        );
}

startTimer(){
  this.timerNextPopy = setTimeout(x => 
  {
    if(this.maxTime <= 0) { }
      this.maxTime -= 1;

    if(this.maxTime>0){
      this.hidevalue = false;
      this.remainingTime = this.determineTimeText()
      this.startTimer();
    }

    else{
      this.hidevalue = true;
    }

  }, 1000);
}

determineTimeText(){
  var days = Math.floor(this.maxTime / 86400);
  var hours = Math.floor((this.maxTime - (days * 86400)) / 3600);
  var minutes = Math.floor((this.maxTime - (hours * 3600)) / 60);
  var seconds = (this.maxTime - (hours * 3600) - (minutes * 60));
  if(days > 0){  
    return days + "j " + hours + "h ";
  }else{
    if(hours > 0){  
      return hours + "h " + minutes + "m ";
    }else{
      if(minutes > 0){
        return minutes + "m " + seconds + "s";
      }else{
        return seconds + "s";
      }
    }
  } ;
}

ionViewDidLoad() {
}

close() {
  this.viewCtrl.dismiss();
}

doRefresh(event){
  this.storage.get('user').then((val) => {
    if (val) {
      clearTimeout(this.timerNextPopy);
      this.loadUserInfo(val.id);
      event.complete();
    }
  });   
}

openPhotoPicker(index) {
  let actionSheet = this.actionSheetCtrl.create({
    title: 'Modifier votre album',
    buttons: [
    {
      text: 'Choisir une photo',
      role: 'destructive',
      handler: () => {
        console.log('Destructive clicked');
        const options: CameraOptions = {
          sourceType: 0,
          quality: 50,
          destinationType: this.camera.DestinationType.DATA_URL,
          encodingType: this.camera.EncodingType.JPEG,
          mediaType: this.camera.MediaType.PICTURE,
          correctOrientation: true
        }
        this.camera.getPicture(options).then((imageData) => {
          let base64Image = 'data:image/jpeg;base64,' + imageData;
          this.user.popies[index].image = base64Image;

          let data: any;
          data = {};
          data.popy = base64Image;

          let loading = this.loadingCtrl.create({
            spinner: 'crescent',
            content: 'Chargement ...',
            dismissOnPageChange: true
          });
          loading.present();

          this.api.post('addPopy/' + this.user.id, data)
          .subscribe(
            (data) => {
              let body: any;
              body = JSON.parse(data.text());
              if(body.error){
                loading.dismiss();
                this.doAlert(body.message);
              }else{
                loading.dismiss();
                this.loadUserInfo(this.user.id);
              }
            },
            (err) => {
              loading.dismiss();
              this.doAlert(err.message);
                        //this.navCtrl.setRoot(this.navCtrl.getActive().component);
                      },
                      () => {
                        //this.goToHome();
                      }
                      );






        }, (err) => {
                          // Handle error
                        });
      }
    },
    {
      text: 'Prendre une photo',
      handler: () => {
        console.log('Archive clicked');
        const options: CameraOptions = {
          sourceType: 1,
          quality: 50,
          destinationType: this.camera.DestinationType.DATA_URL,
          encodingType: this.camera.EncodingType.JPEG,
          mediaType: this.camera.MediaType.PICTURE,
          correctOrientation: true
        }
        this.camera.getPicture(options).then((imageData) => {
                          // imageData is either a base64 encoded string or a file URI
                          // If it's base64 (DATA_URL):
                          let base64Image = 'data:image/jpeg;base64,' + imageData;
                          this.user.popies[index].image = base64Image;

                          let data: any;
                          data = {};
                          data.popy = base64Image;

                          let loading = this.loadingCtrl.create({
                            spinner: 'crescent',
                            content: 'Chargement ...',
                            dismissOnPageChange: true
                          });
                          loading.present();

                          this.api.post('addPopy/' + this.user.id, data)
                          .subscribe(
                            (data) => {
                              let body: any;
                              body = JSON.parse(data.text());
                              if(body.error){
                                loading.dismiss();
                                this.doAlert(body.message);
                              }else{
                                loading.dismiss();
                                this.loadUserInfo(this.user.id);
                              }
                            },
                            (err) => {
                              loading.dismiss();
                              this.doAlert(err.message);
                                        //this.navCtrl.setRoot(this.navCtrl.getActive().component);
                                      },
                                      () => {
                                        //this.goToHome();
                                      }
                                      );

                        }, (err) => {
                          // Handle error
                        });
      }
    },
    {
      text: 'Cancel',
      role: 'cancel',
      handler: () => {
        console.log('Cancel clicked');
      }
    }
    ]
  });

actionSheet.present();


}


removeImage(index) {
  let alert = this.alertCtrl.create({
    title: 'Supprimer cette photo',
    message: 'Tu veux vraiment supprimer cette photo ?',
    buttons: [
    {
      text: 'Annuler',
      role: 'cancel',
      handler: () => {
      }
    },
    {
      text: 'Supprimer',
      handler: () => {
        console.log(this.user.popies[index].id);

        let loading = this.loadingCtrl.create({
          spinner: 'crescent',
          content: 'Suppression ...'
        });
        loading.present();

        let data: any;
        data = {};

        this.api.post('deletePopy/' + this.user.id 
         + '/' +this.user.popies[index].id, data)
        .subscribe(
          (data) => {
            let body: any;
            body = JSON.parse(data.text());
            if(body.error){
              loading.dismiss();
              this.doAlert(body.message);
            }else{
              loading.dismiss();
              this.loadUserInfo(this.user.id);
            }
          },
          (err) => {
            loading.dismiss();
            this.doAlert(err.message);
                      //this.navCtrl.setRoot(this.navCtrl.getActive().component);
                    },
                    () => {
                      //this.goToHome();
                    }
                    );
      }
    }
    ]
  });
  alert.present();
}

goTo(page, direction) {
  this.navCtrl.push(page, {}, {
    direction: direction
  });
}

doAlert(message) {
  let alert = this.alertCtrl.create({
    title: 'AÃ¯e!',
    subTitle: message,
    buttons: ['Ok']
  });

  alert.present();
}

}



