import { Component } from '@angular/core';
import { IonicPage, NavController, LoadingController, App, AlertController } from 'ionic-angular';
import { Api } from '../../providers/api/api';
import {Storage} from "@ionic/storage";
import {ProfilePage} from "../profile/profile";

//import {page} from '../register/register';

@IonicPage()
@Component({
  selector: 'page-login',
  templateUrl: 'login.html',
})
export class LoginPage {

  profilePage = ProfilePage;
  user: any;
  password: any;
  loaded: boolean;

  constructor(public navCtrl: NavController, public app: App, public api: Api, public storage: Storage, public loadingCtrl: LoadingController, public alertCtrl: AlertController) {
    this.loaded = false;
    let loading = this.loadingCtrl.create({
      content: 'Chargement...'
    });
    this.storage.get('isConnected').then((val) => {
      if (val) {
        this.navCtrl.setRoot(ProfilePage);
      }
    });
    this.loaded = true;

    this.storage.get('user').then((val) => {
      if (val) {
        this.user = val;
      }
    });

    loading.dismiss();
  }

  ionViewDidLoad() {

  }

  goTo(page) {
    this.app.getRootNav().setRoot(page)
    .then(() => {

    })
  }

  checkLogin(){
    let data: any;
    data = {};
    data.user = this.user;
    data.password = this.password;

    this.api.post('connectUser', data)
    .subscribe(
      (data) => {
        let body: any;
        body = JSON.parse(data.text());

        console.log(body);
        if(body.error){
          this.showAlertBadAccount(body.message);
        }else{
          this.storage.set('isConnected',true);
          this.storage.set('user', body);
        }
      },
      (err) => {
        console.log(err);
        this.showAlertBadAccount('Votre compte et\/ou mot de passe ne sont pas valides');
                    //alert("ko");
                    //this.navCtrl.setRoot(this.navCtrl.getActive().component);
                  },
                  () => {
                    //this.goToHome();
                  }
                  );

    this.api.post('getConfiguration', data)
    .subscribe(
      (data) => {
        let body: any;
        body = JSON.parse(data.text());

        this.storage.set('configurations', body);

                        // On redirige l'utilisateur vers sa page si il est authentifiÃ©
                        this.navCtrl.setRoot(ProfilePage);


                      },
                      (err) => {
                        this.storage.remove('configurations');
                        this.storage.remove('user');
                        this.storage.remove('isConnected');
                        this.storage.remove('fromLoading');
                        this.navCtrl.setRoot(LoginPage);
                      },
                      () => {
                          //this.goToHome();
                        }
                        );
  }

  showAlertBadAccount(message) {
    const alert = this.alertCtrl.create({
      title: 'Erreur de connexion',
      subTitle: message,
      buttons: ['OK']
    });
    alert.present();
  }

}


